import {newLineEnded} from '../languageMarker';
import {SnippetProviderContext} from '../orchestrator';
import {DocumentInfoWithOffset, LocalImportContextOption} from '../prompt';
import {SnippetProviderType, SnippetSemantics, SnippetWithProviderInfo} from '../snippetInclusion/snippets';
import {SnippetProvider} from './snippetProvider';

export class LocalImportContextSnippetProvider extends SnippetProvider {
    private formSnippets(imports: string[], currentFile: DocumentInfoWithOffset): SnippetWithProviderInfo[] {
        return imports.map(importPath => ({
            provider: SnippetProviderType.LocalImportContext,
            semantics: SnippetSemantics.Snippet,
            snippet: newLineEnded(importPath),
            relativePath: currentFile.relativePath,
            startLine: 0,
            endLine: 0,
            score: 1,
        }));
    }

    public async buildSnippets(context: SnippetProviderContext): Promise<SnippetWithProviderInfo[]> {
        // Note: the fileSystem is only intended for testing, we cannot serialize it
        const {currentFile, options, fileSystem} = context;

        // Short circuit if not enabled
        if (options.localImportContext == LocalImportContextOption.NoContext) return [];

        // This is being proxied to the worker thread because TreeSitter is
        // CPU intensive and can induce latency in the main thread
        const localImportContext = await this.api.extractLocalImportContext(currentFile, fileSystem);
        return this.formSnippets(localImportContext, currentFile);
    }
}
