import {SnippetWithProviderInfo} from '../lib';
import {SnippetProviderContext} from '../orchestrator';
import {NeighboringTabsOption} from '../snippetInclusion/neighboringFiles';
import {SnippetProvider} from './snippetProvider';

export class NeighborFilesProvider extends SnippetProvider {
    async buildSnippets(context: SnippetProviderContext): Promise<SnippetWithProviderInfo[]> {
        const {currentFile, neighborFiles, options, lineCursorHistory} = context;
        if (
            options &&
            neighborFiles &&
            neighborFiles.length &&
            options.neighboringTabs !== NeighboringTabsOption.None
        ) {
            const snippets = await this.api.getNeighborSnippets(
                currentFile,
                neighborFiles,
                options.neighboringSnippetTypes,
                options.neighboringTabs,
                options.snippetSelection,
                options.snippetSelectionK,
                lineCursorHistory,
                options.cursorSnippetsPickingStrategy
            );
            return snippets;
        }
        return [];
    }
}
