import {SnippetProviderContext} from '../orchestrator';
import {SnippetWithProviderInfo} from '../snippetInclusion/snippets';
import {WorkerProxy} from '../workerProxy';

export abstract class SnippetProvider {
    api: WorkerProxy;

    constructor(workerProxy: WorkerProxy) {
        this.api = workerProxy;
    }
    protected abstract buildSnippets(context: SnippetProviderContext): Promise<SnippetWithProviderInfo[]>;

    public getSnippets(context: SnippetProviderContext): Promise<SnippetWithProviderInfo[]> {
        return new Promise(async (resolve, reject) => {
            resolve(this.buildSnippets(context));
        });
    }
}
