import {FileSystem} from './fileSystem';
import {DocumentInfo, DocumentInfoWithOffset, PromptOptions} from './prompt';
import {LineCursorHistory} from './snippetInclusion/neighboringFiles';
import {SnippetWithProviderInfo} from './snippetInclusion/snippets';
import {LanguageSnippetProvider} from './snippetProviders/language';
import {LocalImportContextSnippetProvider} from './snippetProviders/localImportContext';
import {NeighborFilesProvider} from './snippetProviders/neighborFiles';
import {PathSnippetProvider} from './snippetProviders/path';
import {SnippetProvider} from './snippetProviders/snippetProvider';
import {workerProxy} from './workerProxy';

export type SnippetProviderContext = {
    currentFile: DocumentInfoWithOffset;
    neighborFiles: DocumentInfo[];
    options: PromptOptions;
    lineCursorHistory?: LineCursorHistory;
    // The filesystem is primarily intended for usage in tests and main-thread functions
    fileSystem?: FileSystem;
};

const OrchestratorTimeout = 250;
const SnippetProviders = [
    LanguageSnippetProvider,
    PathSnippetProvider,
    NeighborFilesProvider,
    LocalImportContextSnippetProvider,
];

export class SnippetOrchestrator {
    private providers: SnippetProvider[];

    constructor() {
        this.providers = SnippetProviders.map(provider => new provider(workerProxy));
    }

    // Since we don't export the workerProxy from promptlib and
    // instead manage proxying here, we need to expose the functionality to
    // start/stop threading so that it can be managed by the extension
    startThreading = () => workerProxy.startThreading();
    stopThreading = () => workerProxy.stopThreading();

    public async getSnippets(context: SnippetProviderContext): Promise<SnippetWithProviderInfo[]> {
        // Get snippets from each provider

        return new Promise(async (resolve, reject) => {
            const snippets: SnippetWithProviderInfo[] = [];
            // Set a timeout to resolve what has returned when we hit a timeout
            const timeout = setTimeout(() => {
                resolve(snippets);
            }, OrchestratorTimeout);

            snippets.push(
                ...(
                    await Promise.all(
                        this.providers.map(provider => {
                            return provider.getSnippets(context);
                        })
                    )
                ).flat()
            );

            clearTimeout(timeout);
            resolve(snippets);
        });
    }
}
